.App {
  text-align: center;
}

h3 {
  margin-top: 0;
}

.google-map {
  color: red;
  height: 60vh;
  width: 100%;
}

.pin {
  margin: 0 auto;
}

.pin-icon {
  display: inline-block;
  font-size: 1rem;
}

.pin-icon,
.pin-icon:active,
.pin-icon:hover {
  cursor: pointer;
  margin: -0.938rem 0 0 -0.5rem;
}

.pin-text {
  background-color: #fff;
  cursor: pointer;
  display: inline-block;
  font-size: 1.3em;
  font-weight: 500;
  margin: 0 auto;
}

.close {
  background-color: transparent;
  border: 0.063rem solid #000;
  cursor: pointer;
  position: absolute;
    right: 1.25rem;
}

.button-selectors {
  margin: 0 auto;
}

.city-button {
  background: transparent;
  border: none;
  border-left: 0.063rem solid #000;
  font-size: 1rem;
  font-weight: regular;
  margin: 0.313rem auto;
  padding: 0.625rem;
  text-align: center;
}

.city-button:active,
.city-button:hover {
  background-color: #e5f5ff;
  border: none;
  border-left: 0.063rem solid #000;
}

.city-button:first-of-type {
  border-left: none;
}

.city-button.button-selected {
  font-weight: bold;
}

.weather-bar {
  display: block;
  max-height: 7.5rem;
  width: 100%;
}

.daily-weather-container {
  height: 25rem;
  overflow-y: scroll;
}

.weather-time-of-day {
  background-color: #e5f5ff;
  padding: 1.25rem;
}

.is-closed-true {
  display: none;
}

.is-closed-false {
  display: block;
}

.reopen {
  background-color: transparent;
  border: none;
  color: #000;
  display: none;
  margin: 0 auto;
  text-decoration: underline;
}

.reopen:active {
  border: none;
}

.reopen.is-displayed-true {
  display: block;
  padding: 2rem;
}

@media screen and (min-width: 49.938rem) {
  .google-map {
    height: 50vh;
  }

  .map-h2 {
    font-size: 1rem;
    font-weight: 400;
  }

  .pin-icon {
    font-size: 2vw;
  }

  .weather-bar {
    position: relative;
      bottom: 0;
  }

  .daily-weather-container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 20rem;
    overflow-x: scroll;
  }
  
  .weather-time-of-day {
    margin: 1rem;
    min-width: 18.75rem;
  }
}